  /* src/YouTubePlayer.css */
  .youtube-player-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .youtube-video {
    border: 2px solid #ccc;
    border-radius: 10px;
    overflow: hidden;
  }

  .error-message {
    color: red;
    font-size: 1.2em;
    text-align: center;
    margin-top: 20px;
  }

  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }

  .spinner {
    border: 8px solid rgba(0, 0, 0, 0.1); /* Light grey */
  border-top: 8px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
  }

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  /* Additional styles to hide the 'Watch on YouTube', 'Watch Later', and 'Share' icons */
  .video-wrapper {
    position: relative;
    width: 100%;
    max-width: 640px;
    height: 100%;
    max-height: 390px;
    padding-top: 56.25%; /* 16:9 aspect ratio */
  }

  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  /* Hide 'Watch on YouTube' link, 'Watch Later', and 'Share' icons */
  .youtube-iframe::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    background: white; /* Match this to the iframe's background color */
    pointer-events: none;
  }
